import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {TextField, Button} from "@material-ui/core";
import {Container} from "./IndexStyledComponent";

class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: {},
            id: 3
        };
        this.getUserData = this.getUserData.bind(this);
    }

    getUserData(id = this.state.id) {
        var xhr = new XMLHttpRequest();
        xhr.open("GET", `https://reqres.in/api/products/${id}`, true);
        xhr.onload = function(){
            console.log(xhr.responseText);
            this.setState({
                userData: JSON.parse(xhr.responseText)
            });
        };
        xhr.send();
    }

    componentDidMount () {
        console.log("component mounted making api call..");
        this.getUserData(3);
      }

    render() {
        return (
            <Container>
                <React.Fragment>
                    <TextField onChange={(value) => this.setState({id: value})}></TextField>
                    <Button onClick={this.getUserData} variant="contained" color="primary">Get User Data</Button>
                    {
                        !this.state.userData ? null : <React.Fragment>
                            <div>ID: {this.state.userData.id}</div>
                            <div>Name: {this.state.userData.name}</div>
                            <div>Year: {this.state.userData.year}</div>
                            <div>Pantone: {this.state.userData.pantone_value}</div>
                        </React.Fragment>
                    }
                </React.Fragment>
            </Container>
        );
    }
}
ReactDOM.render(<Index />, document.getElementById('root'));



